import style from './MyProject.module.scss';
import HeadShake from 'react-reveal/HeadShake';
import snImage from "../../assets/images/Screenshot_42.jpg";
import counterImage from "../../assets/images/Screenshot_40.jpg";
import portfolioImage from "../../assets/images/Screenshot_39.jpg";
import todoImage from "../../assets/images/Screenshot_41.jpg";
import noImage from "../../assets/images/no-image.png";


const MyProject = ({link, active, projectName, project, description, setActive, ...props}) => {

    const stylesForBackground = {
        socialNetwork: {backgroundImage: `url(${snImage})`},
        counter: {backgroundImage: `url(${counterImage})`},
        portfolio: {backgroundImage: `url(${portfolioImage})`},
        todoList: {backgroundImage: `url(${todoImage})`},
        noPic: {backgroundImage: `url(${noImage})`},
    }

    const lowerCaseName = projectName.toLocaleLowerCase()

    const stylePic = lowerCaseName.indexOf('to-do') > -1 ? stylesForBackground.todoList
        : lowerCaseName.indexOf('social-network') > -1 ? stylesForBackground.socialNetwork
            : lowerCaseName.indexOf('my-portfolio') > -1 ? stylesForBackground.portfolio
                : lowerCaseName.indexOf('counter') > -1 ? stylesForBackground.counter
                    : stylesForBackground.noPic


    const activeStyle = active === projectName

    return (
        <HeadShake>
            <div onMouseEnter={() => setActive(projectName)}
                 className={style.project}>
                <a href={link} className={style.button}>watch</a>
                <div style={stylePic} className={`${style.image} ${activeStyle && style.active}`}>
                </div>
                <div className={style.descriptionBlock}>
                    <h4 className={style.projectName}>{projectName}</h4>
                    <p style={style.description}>{description}</p>
                </div>
            </div>
        </HeadShake>
    )
}
export default MyProject;
